'use client'
import { useAuth } from "@/components/AuthContext" 
import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { DeleteIcon } from "lucide-react"
import { bookService } from "@/lib/services/bookService"


export default function Page(){
    const {isAuthenticated} = useAuth()
    const router = useRouter()
    const [booksData, setBooksData] =useState([])
    const [isLoading,setIsLoading] = useState(false)

    useEffect(()=>{
       if(!isAuthenticated){
            router.push('/login')
        }
    },[isAuthenticated])

    useEffect(()=>{
       setIsLoading(true)
       bookService.getBooks().then((res)=>{
        setIsLoading(false)
        setBooksData(res.data.data)
       }).catch(error=>{
        setIsLoading(false)
        console.log(error)
       })   
    },[])
    return(
        <div className="w-full">
            <div className="flex justify-end px-12 py-4">
              <input className="max-w-md bg-white px-4 py-3 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500" 
              placeholder="search"
              />
            </div>
            <div className="flex justify-center">
<Table className="bg-white">
  <TableCaption>A list of your recent book.</TableCaption>
  <TableHeader>
    <TableRow>
      <TableHead className="">ID</TableHead>
      <TableHead>Title</TableHead>
      <TableHead>Auther</TableHead>
      <TableHead className="">Publish Date</TableHead>
      <TableHead className="">Owner</TableHead>
      <TableHead className="">Action</TableHead>
    </TableRow>
  </TableHeader>
  <TableBody>
    <TableRow>
      <TableCell className="">001</TableCell>
      <TableCell>Paid</TableCell>
      <TableCell>Paid</TableCell>
      <TableCell>Credit Card</TableCell>
      <TableCell className="">$250.00</TableCell>
      <TableCell className = ""><DeleteIcon className="cursor-pointer"/></TableCell>
    </TableRow>
  </TableBody>
</Table>
            </div>
            
        </div>
    )
}